import Vue from 'vue';
import iView from 'iview';
import Util from '../libs/util';
import VueRouter from 'vue-router';
import Cookies from 'js-cookie';
import {routers, otherRouter, appRouter} from './router';

Vue.use(VueRouter);

// 
const RouterConfig = {
    // mode: 'history',
    routes: routers
};

export const router = new VueRouter(RouterConfig);

router.beforeEach((to, from, next) => {
    iView.LoadingBar.start();
    console.log("before each")
    Util.title(to.meta.title);
    if (Cookies.get('locking') === '1' && to.name !== 'locking') { // 
        next({
            replace: true,
            name: 'locking'
        });
    } else if (Cookies.get('locking') === '0' && to.name === 'locking') {
        next(false);
    } else {
        if (!Cookies.get('user') && to.name !== 'login') { // 
            next({
                name: 'login'
            });
        } else if (Cookies.get('user') && to.name === 'login') { // 
            Util.title();
            next({
                name: 'home_index'
            });
        } else {
            // let menuAuthList = Cookies.get('authMenu');
            var curRouterObj = Util.getRouterObjByName([otherRouter, ...appRouter], to.name);
            if (curRouterObj && curRouterObj.auth !== undefined) { // 
                if (curRouterObj.auth) {
                    Util.toDefaultPage([otherRouter, ...appRouter], to.name, router, next); // 
                } else {
                    next({
                        replace: true,
                        name: 'error-403'
                    });
                }
            } else { // , 
                Util.toDefaultPage([...routers], to.name, router, next);
            }
        }
    }
});

router.afterEach((to) => {
    Util.openNewPage(router.app, to.name, to.params, to.query);
    iView.LoadingBar.finish();
    window.scrollTo(0, 0);
});



